$('.sendMsg').on('click', function (e) {
    var c_name = $('#c_name').val();
    var c_email = $('#c_email').val();
    var c_message = $('#c_message ').val();
    var response = $('.ajax-response');

    if (c_name == '' || c_email == '' || c_message == '' || !isValidEmailAddress(c_email)) {
        response.fadeIn(500);
        response.html('<i class="fa fa-warning"></i> 请填写相关的信息后再提交.');
    } else {
        response.html('');
        var subject = "来自客户(" + c_name + ':' + c_email + ")的需求:" + c_message;
        location.href = "mailto:lovelypig5@163.com?subject=" + subject;
    }
});
